<template>
     <div class="imgmain" v-show="showimg">

        <div class="imgcontent" @click.self="close">
          <img :src="imgsrc">
        </div>
       <div class="top">
         <button  class="toleft-button topbutton" @click="pre"></button><div class="topbody">{{n+1}}/{{imgs.length}}</div><button  class="toright-button topbutton" @click="next"></button>
       </div>
       <div class="left"><button class="fancybox-arrow fancybox-arrow--left" @click="pre"></button></div>
       <div class="right"><button class="fancybox-arrow fancybox-arrow--right" @click="next"></button></div>
       <div class="bottom"></div>
     </div>
</template>

<script>

export default {
  props:{imgs:Array,
    shoupage:Number},
  data () {
    return {
      imgsrc:"",
      showimg:true,
      n:0
    }
  },
  mounted:function () {
    this.n = this.shoupage;
    this.imgsrc = this.imgs[this.n];
  },

  watch:{
    shoupage:function (val) {
      console.log("aa");
      this.n = val;
      this.imgsrc = this.imgs[this.n];
    }
  },
  methods:{
    close:function () {
      this.$emit('closeimgs')
    },
     next:function () {
       this.n++;
       if(this.n>=this.imgs.length){
         this.n=0;
       }
       this.imgsrc = this.imgs[this.n];
     },
    pre:function () {
      this.n--;
      if(this.n<0){
        this.n=this.imgs.length-1;
      }
      this.imgsrc = this.imgs[this.n];
    }

  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
.imgmain{
  position: fixed;
  top:0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: rgba(0,0,0,.5);
  z-index: 9999999;
}
.imgmain .imgcontent{
     height: 100%;
     width: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
  }
.imgmain .imgcontent img{
  max-height: 80%;
  width: auto;
  max-width: 80%;
  box-shadow: 5px 5px 20px #aaa;
}
  .left{
    position: absolute;
    width: 60px;
    height: 100%;
    top: 0;
    left: 0;
  }
  .top{
    position: absolute;
    top: 0;
    left: 50%;
    margin-left: -79px;
  }
  .right{
    position: absolute;
    width: 60px;
    height: 100%;
    top: 0;
    right: 0;
  }
  .bottom{
    position: absolute;
    bottom: 60px;
    right:5%;
    width: 90%;
    height: 1px;
    background: #eee;
    z-index: 999999;
  }
  .fancybox-arrow--right{
    transform: rotate(180deg);;
  }
.fancybox-arrow {
  position: absolute;
  top: 50%;
  margin: -50px 0 0;
  height: 100px;
  width: 54px;
  padding: 0;
  border: 0;
  outline: none;
  background: none;
  cursor: pointer;
  z-index: 99995;
  opacity: 1;
  -webkit-user-select: none;
  -moz-user-select: none;
  -ms-user-select: none;
  user-select: none;
  transition: opacity .25s;
}
.fancybox-arrow:after {
  content: "";
  position: absolute;
  top: 28px;
  width: 44px;
  height: 44px;
  background-color: rgba(30,30,30,.8);
  background-image: url(../../assets/icon/toleft.png);
  background-repeat: no-repeat;
  background-position: 50%;
  background-size: 24px 24px;
}
.topbody{
  display: inline-block;
  width: 88px;
  height: 44px;
  line-height: 44px;
  background-color: rgba(30,30,30,.8);
  color: #fff;
  text-align: center;
}
.topbutton {
  background-color: rgba(30,30,30,.8);
  display: inline-block;
  position: relative;
  margin: 0;
  padding: 0;
  border: 0;
  width: 44px;
  height: 44px;
  line-height: 44px;
  text-align: center;
  color: #ddd;
  border-radius: 0;
  cursor: pointer;
  vertical-align: top;
  outline: none;
}
  .toleft-button:after{
    content: "";
    top: 18px;
    left: 19px;
    position: absolute;
    width: 5px;
    height: 5px;
    border: solid 2px;
    border-color: transparent transparent #fff #fff;
    transform: rotate(45deg);
  }
.toright-button:after{
  content: "";
  top: 18px;
  left: 19px;
  position: absolute;
  width: 5px;
  height: 5px;
  border: solid 2px;
  border-color: transparent transparent #fff #fff;
  transform: rotate(-135deg);
}
</style>



