<template>
  <div class="zhuye">
       <div class="actorcontent ">
         <div class="actorItem" v-for="item in actor">
           <div class="actorTop">
              <img :src="item.autor" class="autor">
              <span class="autorname">
                <p class="aname"><span>{{item.name}}</span><span class="loction">深圳</span><span class="loction">web前端</span></p>
                <p class="atime"><span style="color: #2196f3">{{item.title}}</span> {{item.creatTime}}</p>
              </span>
             <span style="float: right"><a :href="item.href" target="_blank" v-if="item.href">相关链接</a></span>
           </div>
           <div class="actorbody">
               <div v-html="item.content" class="actorcon"></div>
               <div class="imgs">
                   <span v-for="(img,n) in item.imags" class="imgitem" @click="showimgs(item.imags,n)">
                     <img v-lazy="img" :key="img">
                   </span>
               </div>
           </div>

           <div class="actorfooter">文章摘抄于: <a :href="item.fromhref" target="_blank">{{item.fromhreftxt}}</a></div>
         </div>
         <pagination :page="pagemess.page" :totalpage="pagemess.totalpage" @changepage="gopage"></pagination>
         <vueImages :imgs="images" :shoupage="n" v-if="imgsshow" @closeimgs="closeimgs"></vueImages>
       </div>
  </div>
</template>
<script>
  import pagedata from '@/dataJson/zhuyeJson'
  import photo from "@/assets/photo.png"
  import pagination from  "@/components/comom/pagination.vue"
  import vueImages from '@/components/comom/imgs.vue'
  export default {
    components:{
      pagination,
      vueImages
    },
  data () {
    return {

      pagemess:{
        totalpage:0,
        page:1
      },
      imgsshow:false,
      n:0,
      images:[],
      actor:[]
    }
  },
  mounted:function () {
    this.getdata()

  },

  methods:{
    closeimgs:function () {
      this.imgsshow = false;
    },
    showimgs:function (imgs,n) {
      this.imgsshow = true;
      this.images = imgs;
      this.n = n;
    },
    gopage:function (page) {
      this.$router.push({path:"zhuye",query:{page:page}});
      this.getdata()
    },
    getdata:function () {
      let page = this.$route.query.page;
      this.pagemess.totalpage = pagedata.length;
      if(!page){
        this.pagemess.page = 1;
        this.actor = pagedata[0]
      }else {
        if(page> pagedata.length || page<1){
          this.pagemess.page = 1;
          this.actor = pagedata[0]
          return
        }
        this.pagemess.page = parseInt(page);
        this.actor = pagedata[page-1];
      }
    }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
.actorItem{
  width: 100%;
  background-color: #fff;
  padding: 16px;
  box-sizing: border-box;
  border-radius: 2px;
  margin-bottom: 20px;
}
  .autor{
    width: 60px;
    height: 60px;
    border-radius: 50%;
    border: solid 1px #eee;
    display: inline-block;
  }
.autorname{
  display: inline-block;
  vertical-align: top;
  margin-left: 10px;
  line-height: 25px;
}
.autorname .aname{
  font-size:16px ;
  font-weight: 600;
}
.loction{
 margin-left: 15px;
  background-color: #FFB800;
  border-radius: 5px;
  font-size: 12px;
  padding:3px 5px;
  font-weight: 100;
  color: #fff;
}
.actorcon{
  font-size: 14px;
  text-indent: 30px;

}
  .imgs{
    overflow: hidden;
    margin: 10px 20px;
  }
  .imgitem{
    overflow: hidden;
    position: relative;
    float: left;
    width: 33.33%;
    box-sizing: border-box;
    padding: 0 10px 20px;
  }
  .imgitem img{
    width: 100%;
    float: left;
    height: auto;
    border-radius: 0;
    /*cursor: url(../images/open_new.cur), auto;*/
    border: 1px solid #f1f1f1;
  }
.actorfooter{
  margin-top: 10px;
  padding-top: 16px;
  border-top: 1px solid #f1f1f1;
  line-height:30px;
  text-align: right;
}
</style>



